/*Topnav*/
.topnav_wrapper{
    z-index: 4;
    background-color: #FFF;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    min-height: 10vh;
    display: flex;
    justify-content: space-between;
}

.topnav_wrapper .topnav{
    z-index: 3;
    position: relative;
    width: 92%;
    margin: 0 auto;
    display: none;
    align-items: center;
    justify-content: space-between;
    background-color: white;

}

.topnav_wrapper .btn.shop{
    border: solid 4px;
    padding: 5px 25px;
    cursor: pointer;
    font-family: "CartographCF";
    font-size: 1.3rem;
}

.topnav_wrapper .logo svg{
    max-height: 5vh;
    width: auto;
    cursor: pointer;
}

.topnav_wrapper .extra{
    display: flex;
    align-items: center;
    gap: 100px;
}

.topnav_wrapper .user svg{
    max-width: 5vh;
    cursor: pointer;
}

.topnav_wrapper .cartBtn{
    position: relative;
}

.topnav_wrapper .cartBtn svg{
    max-width: 5vh;
    cursor: pointer;
}

.topnav_wrapper .qtyCart{
    position: absolute;
    top: 5px;
    right: 10px;
}

.topnav_wrapper .cart{
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    visibility: hidden;
}

.topnav_wrapper .cart.active{
    visibility: visible;
}

.topnav_wrapper .cart .container{
    width: 25vw;
    height: 85vh;
    position: absolute;
    right: -30vw;
    background-color: #FFF;
    padding: 15vh 2.5vw 0vh 2.5vw;
    display: flex;
    transition: ease-in-out .5s;
    flex-direction: column;
    gap: 30px;
    z-index: 0;
    overflow: scroll;
}

.topnav_wrapper .cart.active .container{
    right: 0;
    transition: ease-in-out .5s;
}

.topnav_wrapper .square{
    position: absolute;
    right: -4vw;
    background-color: #FFF;
    top: 0;
    height: 10vh;
    width: 200px;
    z-index: -1;
}

.topnav_wrapper .menu{
    background-color: #FFF;
    bottom: 0;
    right: -4vw;
    position: absolute;
    display: flex;
    flex-direction: column;
    /* display: none; */
    z-index: -2;
    transition: .4s;
}

.topnav_wrapper .menu.active{
    transition: .6s;
    bottom: -10vh;
    display: block;
}

.topnav_wrapper .menu .btn{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 5vh;
    width: 10vw;
    font-family: "CartographCF";
    cursor: pointer;
}

.topnav_wrapper .login {
    background-color: #42C56F   ;
}

.topnav_wrapper .login:hover {
    background-color: #00cc47   ;
}

.topnav_wrapper .topnav.unlogged .menu{
    color:#FFF;
}

.topnav_wrapper .topnav.active{
    display: flex;
}

.topnav_wrapper .popup {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 4;
    visibility: hidden;
}

.topnav_wrapper .overflow {
    height: 100vh;
    width: 100vw;
    background-color: #00000087;
    position: absolute;
}

.topnav_wrapper .popup .grid{
    width: 100%;
}

.topnav_wrapper .popup .grid .ind{
    z-index: 4;
}

.topnav_wrapper .popup .closeBtn {
    z-index: 5;
    transform: rotate(45deg);
    position: absolute;
    top: 40px;
    right: 40px;
    
}

.topnav_wrapper .popup .closeBtn svg{
    width: 3vw;
    height: 3vw;
    cursor: pointer;
}

.topnav_wrapper .popup.visible{
    visibility: visible;
}

.topnav_wrapper .form{
    z-index: 4;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.form form{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    background-color: #464646;
    padding: 50px;
    
}

.topnav_wrapper .colors{
    display: flex;
    width: 20vw;
    box-sizing: border-box;
}

.topnav_wrapper .colors div{
    aspect-ratio: 1/1;
    width: 20%;
    min-width: 0!important;
    display: flex;
    align-items: center;
    justify-content: center;

}

.topnav_wrapper .colors div:hover svg {
    display: block;
}

.topnav_wrapper .colors.purple .purple svg,
.topnav_wrapper .colors.red .red svg,
.topnav_wrapper .colors.orange .orange svg,
.topnav_wrapper .colors.green .green svg,
.topnav_wrapper .colors.sky .sky svg,
.topnav_wrapper .colors.violet .violet svg{
    display: block;
}

.topnav_wrapper .colors.purple{
    border: solid 6px var(--purple);
}

.topnav_wrapper .colors.red{
    border: solid 6px var(--red);
}

.topnav_wrapper .colors.orange{
    border: solid 6px var(--orange);
}

.topnav_wrapper .colors.green{
    border: solid 6px var(--green);
}

.topnav_wrapper .colors.sky{
    border: solid 6px var(--sky);
}

.topnav_wrapper .colors.violet{
    border: solid 6px var(--violet);
}

.topnav_wrapper .colorLabel{
    max-width: 20vw;
    color: #FFF;
    display: flex;
    align-items: end;
    gap: 10px;
}

.topnav_wrapper .colorLabel svg{
    width: 20%;
    height: 100%;
}

.topnav_wrapper .colors div svg {
    display: none;
    height: 75%;
    width: 75%;
}

.form form .error {
    background-color: red;
    aspect-ratio: 1/1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    width: 20%;
}

.form form .error .errormsg {
    display: none;
    position: absolute;
    top: -25px;
    right: 0;
    background-color: red;
    padding: 5px;
}

.form form .error:hover .errormsg {
    display: block;
}

.form form input[type=text], .form form input[type=password]{
    background-color: transparent;
    border: none;
    border-bottom: 4px solid #8FC98E;
    resize: none;
    position: relative;
    min-height: 4vh;
    color: #FFF;
    font-family: 'GothamBook';
    width: 100%;
}


.topnav_wrapper .password, .topnav_wrapper .name, .topnav_wrapper .repeat.password, .topnav_wrapper .mail, .topnav_wrapper .title, .topnav_wrapper .help, .topnav_wrapper .submit {
    position: relative;
    min-width: 20vw;
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

.form form .placeholder{
    bottom: 16px;
    position: absolute;
    left: 5px;
    color: #8D8D8D;
    font-family: 'CartographCFBold';
    font-size: 1.1rem;
    transition-duration: .5s;
}

.form form .placeholder.focused{
    bottom: 50px;
    left: 0;
    color: var(--green);
    transition-duration: .5s;
}

.form form .msg .placeholder{
    top: 130px;
    position: absolute;
    left: 5px;
    color: #8D8D8D;
    font-family: 'CartographCFBold';
    font-size: 1.1rem;
    transition-duration: .5s;
}

.form form .msg .placeholder.focused{
    top: -10px;
    left: 0;
    color: var(--green);
    transition-duration: .5s;
}

.form form .btn{
    background-color: var(--green);
    border: none;
    font-family: 'CartographCF';
    color: #FFF;
    padding: 10px;
    font-size: 1.1rem;
    cursor: pointer;
    width: 100%;
}

.form form .btn.cancel{
    background-color: var(--red);
    text-align: center;
}

.form form .btn.cancel:hover{
    background-color: var(--redHover)!important;
}

.form form .btn:hover{
    background-color: var(--greenHover)!important;
}

.form .title{
    color: #FFF;
    font-size: 1.2rem;
}

.form .help {
    color: var(--green);
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.form .help div{
    cursor: pointer;
}

.form .help div:hover{
    color: var(--greenHover);
    
    text-decoration: underline;
}

