.home .footer .copyright p{
    color: #FFF;
    background-color: #000;
    padding: 10px 20px;
    font-size: 1.3rem;
    font-family: 'CartographCFBold';
}

.container .item .border {
    height: 19vh;
    width: 19vh;
    border: solid 5px black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.border img {
    height: 17vh;
    width: 17vh;
    border: solid black 5px;
}

.container .item{
    display: flex;
    width: 100%;
    justify-content: space-between;
    max-height: 25vh;
}

.container .item .data{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.container .item .data .top{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.container .item .data .top .title, .container .item .data .price{
    font-size: 2.5rem;
    font-family: 'CartographCFBold';
    min-width: auto;
}

.container .item .data .top .extraData{ 
    display: flex;
    flex-direction: column;
    font-size: .9em;
    font-family: 'GothamBook';
}

.buyout.layout{
    position: sticky;
    bottom: 0;
    background: #FFF;
    width: 100%;
    height: 200px;
    padding: 4vh 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buyout .btn{
    font-family: 'CartographCF';
    width: 100%;
    text-align: center;
    color: white;
    padding: 20px;
    font-size: 1.2rem;
    cursor: pointer;
}

.item svg{
    padding-top: 30px;
    cursor: pointer;
}

.home .prods .title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    font-size: 1.5rem;
    color: #FFF;
}

.home .prods .title svg {
    height: 4vh;
    width: auto;
}

.home .grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    padding: 5vh 10vw;
    gap: 20px;
}

.home .grid .ind{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.home .grid .ind img{
    height: 14vw;
    width: 14vw;
    border: solid #fff 15px;
    box-sizing: border-box;
}

.home .grid .ind .info{
    background-color: #FFF;
    position: relative;
    width: 14vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: -15px;
}

.home .grid .ind .info .name{
    width:65%;
    display: flex;
    justify-content: center;
    word-break: break-word;
    font-family: 'CartographCFBold';
    font-size: 1.4rem;
    padding: 15px;
    align-items: center;
    text-align: center;
}

.home .grid .ind .info .btn{
    width: 35%;
    background-color: #000;
    color: #fff;
    display: flex;
    justify-content: center;
    height: 100%;
    align-items: center;
    font-size: 1.2rem;
    cursor: pointer;
}

.home .popup {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 4;
    visibility: hidden;
}

.home .popup.visible .closeBtn {
    top: 40px;
    transition: .2s ease;
    transition-delay: .4s;
}

.home .popup.visible .grid {
    top: 0;
    transition: ease-in-out .5s;
}

.home .popup .grid {
    position: relative;
    top: 90vh;
    transition: ease-in-out .2s;
    transition-delay: .2s;
}

.home .overflow {
    height: 100vh;
    width: 100vw;
    background-color: #00000087;
    position: absolute;
    transition: .2s ease-in;
    transition-delay: .2s;
    top: 100vh;
}

.home .popup.visible .overflow {
    top: 0;
    transition: .4s ease-out;
}

.home .popup .grid{
    width: 100%;
}

.home .popup .grid .ind{
    z-index: 4;
}

.home .popup .closeBtn {
    z-index: 5;
    transform: rotate(45deg);
    position: absolute;
    top: -6vh;
    right: 40px;
    transition-duration: .2s;
}

.home .popup .closeBtn svg{
    width: 3vw;
    height: 3vw;
    cursor: pointer;
}

.home .popup.visible{
    visibility: visible;
}

/*Store*/

.store{
    margin-top: 15vh;
    width: 100%;
}

.store .grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    padding: 5vh 10vw;
    gap: 20px;
}

.store .grid .sample{
    
    background-color: white;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    border: solid black 5px;
    cursor: pointer;
}

.store .grid .sample .photo img{
    aspect-ratio: 1/1;
    width: 100%;
    border: solid black 4px;
    box-sizing: border-box;
}

.store .grid .sample .author {
    font-size: .9rem;
    font-family: 'GothamBook';
}

.store .grid .sample .name {
    font-size: 1.3rem;
    font-family: 'CartographCFBold';
    word-break: break-word;
}

.store .filters{
    padding: 0 10vw;
    display: flex;
    align-items: center;
    gap: 40px;
}

.store .filters .btn{
    color: #FFF;
    border: solid 4px #FFF;
    padding: 10px 30px;
    font-family: 'CartographCF';
    cursor: pointer;
    background-color: var(--background);
    z-index: 2;
}

.store .filters .btn:hover{
    background-color: var(--red);
}

.store .filter {
    color: #fff;
    font-family: 'CartographCF';
    position: relative;
    cursor: pointer;
}

.store .filter:hover {
    text-decoration: underline;
}

.store .filters.active .filter {
    transition-duration: .5s;
    left: 0!important;
}

/*Product*/

.product{
    margin-top: 12vh;
    width: 100%;
}

.product .data {
    display: flex;
    padding: 5vh 10vw 3vh 10vw;
    gap: 30px;
}

.product .photoWrapper {
    background-color: #FFF;
    padding: 10px;
    border: solid #000 5px;
}

.product .photoWrapper img {
    aspect-ratio: 1/1;
    width: 100%;
    border: solid #000 5px;
    box-sizing: border-box;
}

.product .data .info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: #FFF;
    gap: 10px;
    max-width: 65%;
}

.product .info .genre {
    font-weight: 600;
    text-transform: uppercase;
}

.product .info .title {
    font-size: 6em;
    font-family: 'CartographCFBold';
}

.product .info .moreInfo {
    display: flex;
    gap: 10px;
    align-items: center;
}

.product .info .moreInfo .author {
    text-decoration: underline;
    cursor: pointer;
}

.product .info .moreInfo div {
    font-weight: 600;
    font-size: 1.1rem;
}

.product .info .moreInfo svg {
    width: 30px;
    height: 30px;
}

.product .info .moreInfo .time {
    font-family: 'GothamBook';
    font-weight: 300;
    font-size: .9rem;
    color: #ffffff91;
}

.product .info .desc {
    font-family: 'GothamBook';
    font-size: 1.1rem;
    color: #FFFFFFC2;
}

.product .info .actions {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 30px;
}

.product .info .actions .buyNow {
    padding: 20px 60px;
    font-size: 20px;
    cursor: pointer;
    font-weight: 600;
}

.product .info .actions .btnFav, .product .info .actions .btnATC {
    background-color: #FFF;
    padding: 10px;
    cursor: pointer;
}

.product .info .actions svg {
    width: 40px;
    height: 40px;
}

.product .info .actions .btnATC {
    position: relative;
}

.product .info .actions .btnATC .plus {
    color: #000;
    position: absolute;
    top: 15px;
    right: 19px;
}

.product .extraInfo .instrument {
    display: flex;
    position: relative;
    height: 24vh;
    align-items: center;
    justify-content: center;
}

.product .extraInfo {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    padding: 0 10vw;
}

.product .extraInfo .instrument .photo img{
    width: 10vw;
}

.product .extraInfo .dataInst{
    display: flex;
    color: #FFF;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    height: 100%;
    left: 70px;
    width: 30%;
    font-family: 'CartographCF';
}

.product .extraInfo .dataInst .qty{
    font-size: 1.6rem;
}

/*Profile*/
.profile {
    margin-top: 15vh;
    width: 100%;
}

.profile .colors{
    display: flex;
    width: 20vw;
    box-sizing: border-box;
}

.profile .colors div{
    aspect-ratio: 1/1;
    width: 20%;
    min-width: 0!important;
    display: flex;
    align-items: center;
    justify-content: center;

}

.profile .colors div:hover svg {
    display: block;
}

.profile .colors.purple .purple svg,
.profile .colors.red .red svg,
.profile .colors.orange .orange svg,
.profile .colors.green .green svg,
.profile .colors.sky .sky svg,
.profile .colors.violet .violet svg{
    display: block;
}

.profile .colors.purple{
    border: solid 6px var(--purple);
}

.profile .colors.red{
    border: solid 6px var(--red);
}

.profile .colors.orange{
    border: solid 6px var(--orange);
}

.profile .colors.green{
    border: solid 6px var(--green);
}

.profile .colors.sky{
    border: solid 6px var(--sky);
}

.profile .colors.violet{
    border: solid 6px var(--violet);
}

.profile .colorLabel{
    max-width: 20vw;
    color: #FFF;
    display: flex;
    align-items: end;
    gap: 10px;
    justify-content: center;
}

.profile .colorLabel svg{
    width: 20%;
    height: 100%;
}

.profile .colors div svg {
    display: none;
    height: 75%;
    width: 75%;
}

.profile .options .colorSelector, 
.profile .options .nameChanger, 
.profile .options .passChanger {
    display: flex;
    flex-direction: column;
    gap: 70px;
    align-items: center;
    justify-content: center;
    border: solid 5px #FFF;
    padding: 40px;
}


.profile input[type=text]{
    background-color: transparent;
    border: none;
    border-bottom-color: currentcolor;
    border-bottom-style: none;
    border-bottom-width: medium;
    resize: none;
    position: relative;
    min-height: 4vh;
    color: #FFF;
    font-family: 'GothamBook';
    width: 100%;
}

.profile .password, .profile .name, .profile .repeat.password, .profile .mail, .profile .title, .profile .help, .profile .submit {
    position: relative;
    min-width: 20vw;
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

.profile .placeholder{
    bottom: 16px;
    position: absolute;
    left: 5px;
    color: #8D8D8D;
    font-family: 'CartographCFBold';
    font-size: 1.1rem;
    transition-duration: .5s;
}

.profile .placeholder.focused{
    bottom: 50px;
    left: 0;
    transition-duration: .5s;
}

.profile .msg .placeholder{
    top: 130px;
    position: absolute;
    left: 5px;
    color: #8D8D8D;
    font-family: 'CartographCFBold';
    font-size: 1.1rem;
    transition-duration: .5s;
}

.profile .passLabel, .profile .nameLabel{
    color: #fff;
    font-family: 'CartographCF';
    font-size: 1.2rem;
}

.profile .btnSq {
    background-color: red;
    aspect-ratio: 1/1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    width: 20%;
    cursor: pointer;
}


.profile .options{
    display: flex;
    justify-content: space-around;
}

.profile .purple input[type=text]{ 
    border-bottom: 4px solid var(--purple)!important;
}

.profile .red input[type=text]{ 
    border-bottom: 4px solid var(--red)!important;
}

.profile .orange input[type=text]{ 
    border-bottom: 4px solid var(--orange)!important;
}

.profile .green input[type=text]{ 
    border-bottom: 4px solid var(--green)!important;
}

.profile .sky input[type=text]{ 
    border-bottom: 4px solid var(--sky)!important;
}

.profile .violet input[type=text]{ 
    border-bottom: 4px solid var(--violet)!important;
}

.profile .title{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
    color: #FFF;
    font-family: 'Gotham';
    font-weight: 500;
    font-size: 1.5rem;
    gap: 10px;
}

.profile .title span{
    font-family: 'GothamBoldItalic';
}


.profile .popup {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 4;
}

.profile .overflow {
    height: 100vh;
    width: 100vw;
    background-color: #00000087;
    position: absolute;
}

.profile .popup .grid{
    width: 100%;
}

.profile .popup .grid .ind{
    z-index: 4;
}

.profile .popup .closeBtn {
    z-index: 5;
    transform: rotate(45deg);
    position: absolute;
    top: 40px;
    right: 40px;
    
}

.profile .popup .closeBtn svg{
    width: 3vw;
    height: 3vw;
    cursor: pointer;
}

.profile .form .help {
    display: flex;
    gap: 10px;
    max-width: 20vw;
}

.profile .form .help .alertBox{
    background-color: #EFF400;
    aspect-ratio: 1/1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 1.2rem;
    width: 15%;
}

.profile .form .help .alertMsg{
    color: #EFF400;
    font-family: 'GothamBook';
}

.profile .form form{
    background-color: var(--background);
    border: solid 6px #FFF;
}

.profile .form  {
    z-index: 4;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.profile .form form .title{
    max-width: 20vw;
    text-align: center;
    flex-direction: column;
    font-family: 'CartographCF';
    font-size: 1.2rem;
    padding: 0;
    gap: 5px;
    font-weight: 400;
}

.profile .form form .help{
    flex-direction: row;
    color: #000;
}

.profile .form .help div:hover{
    text-decoration: none;
    cursor: default;
}

.profile .form .help .alertBox:hover{
    color: #000;
}


.profile .form .response {
    width: 20vw;
    /* background-color: red; */
    justify-content: center;
    display: flex;
    padding: 10px 56px;
    color: #FFF;
    font-family: 'GothamBook';
    visibility: hidden;
    text-align: center;
    transition-duration: .5s;
}


.profile .form .response.visible {
    visibility: visible;
}

.profile .form .response.success {
    background-color: #00ca47;
}

.profile .form .response.error {
    background-color: #ca0000;
}